.button {
  width: 130px;
  height: 44px;
  overflow: hidden;
  border: solid 1px;
  border-radius: 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: $text-white-color;
  background-color: transparent;
  border-color: $text-white-color;
  transition: background-color 0.25s ease, color 0.25s ease,
    box-shadow 0.25s ease, border-color 0.25s ease;
  &:hover,
  &:focus {
    border-color: $accent-lighter-color;
    color: $primaryLightColor;
    background-color: $accent-lighter-color;
    box-shadow: 0px 8px 43px $btn-hover-color,
      inset 0px 0px 6px 6px rgba(0, 0, 0, 0.1);
  }
  &:active {
    box-shadow: none;
  }
  @media screen and (min-width: 768px) {
    width: 150px;
  }
}
.button--active {
  background-color: $accent-lighter-color;
  border: none;
}
.button--rightLocation {
  margin-left: 20px;
  @media screen and (min-width: 768px) {
    margin-left: 30px;
  }
  @media screen and (min-width: 1024px) {
    margin-left: 15px;
  }
}
