.library {
  padding-top: 20px;
  padding-bottom: 20px;
  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  @media screen and (min-width: 1024px) {
    padding-top: 32px;
  }
  &__list {
    @media screen and (max-width: 767.5px) {
      margin-bottom: -20px;
    }
    @media screen and (min-width: 768px) {
      display: flex;
      flex-wrap: wrap;
      margin-left: -32px;
      margin-bottom: -32px;
    }
    @media screen and (min-width: 1024px) {
      margin-left: -16px;
    }
  }
